"use client";

import { useEffect, useState } from "react";
import DataTable, { TableColumn } from "react-data-table-component";

// ==========================
// TypeScript Device Type
// ==========================
type Device = {
  id: number;
  tid: string;
  mid: string;
  merchant_name: string;
  pos_serial: string;
  model: string;
  city: string;
  location: string;
  merchant_address: string;
  telephone: string;
  created_time: string;
};

const DevicesPage = () => {
  const [devices, setDevices] = useState<Device[]>([]);
  const [filteredDevices, setFilteredDevices] = useState<Device[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>("");

  // Filter states
  const [tidFilter, setTidFilter] = useState<string>("");
  const [cityFilter, setCityFilter] = useState<string>("");
  const [locationFilter, setLocationFilter] = useState<string>("");
  const [posSerialFilter, setPosSerialFilter] = useState<string>("");
  const [modelFilter, setModelFilter] = useState<string>("");

  // ==========================
  // Fetch Devices from FastAPI
  // ==========================
  useEffect(() => {
    const fetchDevices = async () => {
      try {
        const response = await fetch("http://127.0.0.1:8000/all-devices");
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
        const data = await response.json();
        setDevices(data.data);
        setFilteredDevices(data.data);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchDevices();
  }, []);

  // ==========================
  // Filter Logic
  // ==========================
  useEffect(() => {
    const filtered = devices.filter((dev) => {
      const matchTid = dev.tid.toLowerCase().includes(tidFilter.toLowerCase());
      const matchCity = dev.city.toLowerCase().includes(cityFilter.toLowerCase());
      const matchLocation = dev.location.toLowerCase().includes(locationFilter.toLowerCase());
      const matchPosSerial = dev.pos_serial.toLowerCase().includes(posSerialFilter.toLowerCase());
      const matchModel = dev.model.toLowerCase().includes(modelFilter.toLowerCase());
      return matchTid && matchCity && matchLocation && matchPosSerial && matchModel;
    });
    setFilteredDevices(filtered);
  }, [tidFilter, cityFilter, locationFilter, posSerialFilter, modelFilter, devices]);

  // ==========================
  // Table Columns
  // ==========================
  const columns: TableColumn<Device>[] = [
    {
      name: "SL",
      selector: (_row, index) => index + 1,
      width: "60px",
      sortable: false,
    },
    { name: "TID", selector: (row) => row.tid, sortable: true },
    { name: "MID", selector: (row) => row.mid, sortable: true },
    { name: "Merchant Name", selector: (row) => row.merchant_name, sortable: true },
    { name: "POS Serial", selector: (row) => row.pos_serial, sortable: true },
    { name: "Model", selector: (row) => row.model, sortable: true },
    { name: "City", selector: (row) => row.city, sortable: true },
    { name: "Location", selector: (row) => row.location },
    { name: "Address", selector: (row) => row.merchant_address },
    { name: "Telephone", selector: (row) => row.telephone },
    {
      name: "Created Time",
      selector: (row) => new Date(row.created_time).toLocaleString(),
      sortable: true,
    },
  ];

  // ==========================
  // Compact Table Styles
  // ==========================
  const customStyles = {
    rows: {
      style: {
        minHeight: "32px",
        paddingTop: "2px",
        paddingBottom: "2px",
      },
    },
    headCells: {
      style: {
        padding: "8px",
        fontWeight: "bold",
        backgroundColor: "#f3f4f6",
      },
    },
    cells: {
      style: {
        padding: "6px 8px",
      },
    },
  };

  if (loading) return <p>Loading devices...</p>;
  if (error) return <p className="text-red-500">Error: {error}</p>;

  // ==========================
  // UI Rendering
  // ==========================
  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">All Devices</h1>

      {/* Filter Inputs */}
      <div className="grid grid-cols-1 md:grid-cols-6 gap-4 mb-4">
        <input
          type="text"
          placeholder="Filter by TID"
          value={tidFilter}
          onChange={(e) => setTidFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <input
          type="text"
          placeholder="Filter by City"
          value={cityFilter}
          onChange={(e) => setCityFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <input
          type="text"
          placeholder="Filter by Location"
          value={locationFilter}
          onChange={(e) => setLocationFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <input
          type="text"
          placeholder="Filter by POS Serial"
          value={posSerialFilter}
          onChange={(e) => setPosSerialFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <input
          type="text"
          placeholder="Filter by Model"
          value={modelFilter}
          onChange={(e) => setModelFilter(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2 w-full"
        />
        <button
          onClick={() => {
            setTidFilter("");
            setCityFilter("");
            setLocationFilter("");
            setPosSerialFilter("");
            setModelFilter("");
          }}
          className="bg-gray-200 px-4 py-2 rounded hover:bg-gray-300 font-medium"
        >
          Clear Filters
        </button>
      </div>

      {/* DataTable */}
      <div className="bg-white rounded-lg shadow-md p-2">
        <DataTable
          columns={columns}
          data={filteredDevices}
          pagination
          highlightOnHover
          striped
          dense
          persistTableHead
          customStyles={customStyles}
        />
      </div>
    </div>
  );
};

export default DevicesPage;
